.page-league-navigation {
  background-color: @primaryColor;
}

.page-league-navigation-item {
  display: inline-block;
  color: #fff;
  padding: 6px 15px 2px 15px;
}

.page-league-navigation-item-selected {
  background-color: @darkPrimaryColor;
  font-weight: 600;
}

.page-league-content {
  margin-top: 15px;
}

.member-list-item {
  cursor: default;
}

.league-list-column {
  border-bottom: 1px #fff solid;
  padding-left: 10px;
  padding-top: 2px;
}

.league-list-column-small {
  width: 100px;
  text-align: center;
}

.league-list-handle[data-sortname=displayname]:before {
  content: '\f007';
}

.league-list-handle[data-sortname=member]:before {
  content: '\f017';
}

.league-member-action {
  float: right;
  cursor: pointer;
  font-size: 0.9em;
  color: #666;
  margin-right: 10px;
}

.league-member-action:hover {
  color: #000;
}

.league-member-action:before {
  font-family: FontAwesome;
  font-size: 0.9em;
  margin-right: 3px;
}

.league-member-confirm:before {
  content: '\f00c';
}

.league-member-deny:before {
  content: '\f05e';
}

.league-member-delete:before {
  content: '\f00d';
}