.roaster {
  -webkit-user-select: none;
  user-select: none;  
}

.roaster-container {
  position: relative;
}

.roaster-girl {
  position: relative;
  background-image: url('/images/ajsa_face.png');
  background-size: cover;
  background-position: 50%;
  height: 75px;
  width: 75px;
  -webkit-user-select: none;
  user-select: none;
  transform: translate3d(0px, 0px, 0px);
}

.roaster-girl:hover {
  cursor: -webkit-grab;
  cursor: grab;
}

.roaster-girl:active {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}

.roaster-overlay {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  height: 100%;
  width: 100%;
  background-color: #000;
  opacity: 0;
  transition: opacity 0.3s;
  pointer-events: none;
}

.animate {
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}

.roaster-overlay-menu {
  position: fixed;
  top: 100%;
  left: 0;
  z-index: 3;
  margin-top: -300px;
  background-color: @darkPrimaryColor;
  text-align: center;
  width: 100%;
  transition: margin-top 0.3s;
}

.roaster-overlay-category {
  background-color: @primaryColor;
  color: @primaryTextColor;
  display: inline-block;
  width: 300px;
  height: 80px;
  text-align: left;
  line-height: 80px;
  font-size: 2em;
  white-space: nowrap;
  overflow: hidden;
  font-weight: 600;
  margin: 15px;
  opacity: 0.8;
}

.roaster-overlay-category:hover {
  opacity: 1;
}

.roaster-overlay-category:before {
  font-family: FontAwesome;
  background-color: @primaryTextColor;
  color: #fff;
  float: left;
  line-height: 80px;
  width: 80px;
  height: 80px;
  font-size: 1em;
  font-weight: normal;
  text-align: center;
  margin-right: 20px;
}

.roaster-manager:before {
  content: '\f007';
}

.roaster-freeagent:before {
  content: '\f1ea';
}